html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

picture {
  display: block;
  line-height: 0; }

body {
  min-width: 320px;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  text-align: left;
  color: #011c40; }

.logo {
  margin-top: 0;
  margin-bottom: 0;
  display: block; }
  @media (max-width: 767px) {
    .logo {
      margin-left: 15px; } }

@media (max-width: 1023px) {
  .logo__icon {
    width: 38px;
    height: 20px;
    background-size: 38px 20px; } }

@media (max-width: 767px) {
  .logo__icon {
    width: 42px;
    height: 22px;
    background-size: 42px 22px; }
    .logo__icon--no-js {
      stroke: #011c40;
      color: #011c40; } }

.logo__icon--blue {
  stroke: #011c40;
  color: #011c40; }

.page-header {
  width: 100%;
  padding: 35px 103px 0 103px;
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  z-index: 1;
  margin-left: auto;
  margin-right: auto; }
  @media (max-width: 1023px) {
    .page-header {
      padding: 30px 45px 0 45px; } }
  @media (max-width: 767px) {
    .page-header {
      padding: 20px 0 0; }
      .page-header--no-js {
        position: relative;
        background-color: #f9fbfd; } }
  .page-header--white {
    position: relative;
    background-color: #f9fbfd; }

.page-header__navigation {
  display: flex;
  margin-left: auto; }
  @media (max-width: 767px) {
    .page-header__navigation {
      display: none;
      width: 100%;
      height: calc(100vh - 49px);
      position: absolute;
      top: 49px;
      left: 0;
      padding: 50px 15px;
      background-color: #f9fbfd; }
      .page-header__navigation--show {
        display: flex;
        z-index: 2; }
      .page-header__navigation--no-js {
        display: flex;
        position: relative;
        top: 0;
        height: unset; } }

.page-header__sandwich {
  display: none;
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer; }
  .page-header__sandwich::after {
    content: "";
    position: relative;
    top: -3px;
    width: 24px;
    height: 14px;
    background-image: url("../img/icon-menu.svg");
    background-repeat: no-repeat; }
  .page-header__sandwich--close {
    background-color: transparent; }
    .page-header__sandwich--close::after {
      content: "";
      width: 15px;
      height: 15px;
      background-image: url("../img/icon-close.svg"); }
  @media (max-width: 767px) {
    .page-header__sandwich {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 24px;
      height: 14px;
      margin-left: auto;
      margin-right: 15px; }
      .page-header__sandwich--no-js {
        display: none; } }

.main-menu {
  display: flex;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  color: #f9fbfd;
  list-style: none;
  padding: 0;
  margin: 0; }
  @media (max-width: 1023px) {
    .main-menu {
      flex-direction: row;
      font-size: 14px;
      line-height: 17px; } }
  @media (max-width: 767px) {
    .main-menu {
      width: 100%;
      flex-direction: column;
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
      background-color: #f9fbfd; }
      .main-menu--no-js {
        flex-direction: column; } }

.main-menu__item {
  margin-left: 34px; }
  .main-menu__item:first-child {
    margin-left: 0; }
  @media (max-width: 1023px) {
    .main-menu__item {
      margin-left: 24px; } }
  @media (max-width: 767px) {
    .main-menu__item {
      width: 100%;
      margin-left: 0;
      margin-top: 30px; }
      .main-menu__item:first-child {
        margin-top: 0; } }

.main-menu__link {
  text-decoration: none;
  color: #f9fbfd; }
  .main-menu__link:visited {
    color: #f9fbfd; }
  .main-menu__link[href] {
    color: #f9fbfd; }
    .main-menu__link[href]:visited {
      color: #f9fbfd; }
    .main-menu__link[href]:hover {
      color: #f9fbfd; }
  @media (max-width: 767px) {
    .main-menu__link {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      color: #f9fbfd; }
      .main-menu__link[href] {
        color: #011c40; }
        .main-menu__link[href]:visited {
          color: #011c40; } }

.promo {
  width: 100%;
  height: 650px;
  position: relative;
  z-index: 0;
  color: #f9fbfd;
  background: url("../img/header-bg-desktop@1x.jpg") no-repeat 50% 50%/cover transparent #002f6d; }
  @media (min-resolution: 1.5dppx) {
    .promo {
      background-image: url("../img/header-bg-desktop@2x.jpg"); } }
  @media (max-width: 1023px) {
    .promo {
      background-image: url("../img/header-bg-tablet@1x.jpg"); } }
  @media (max-width: 1023px) and (min-resolution: 1.5dppx) {
    .promo {
      background-image: url("../img/header-bg-tablet@2x.jpg"); } }
  @media (max-width: 767px) {
    .promo {
      background-image: url("../img/header-bg-mobile@1x.jpg"); } }
  @media (max-width: 767px) and (min-resolution: 1.5dppx) {
    .promo {
      background-image: url("../img/header-bg-mobile@2x.jpg"); } }

.page-footer {
