.page-footer__menulink {
  text-decoration: none;
  color: #f9fbfd; }
  .page-footer__menulink:visited {
    color: #f9fbfd; }
  .page-footer__menulink[href] {
    color: #f9fbfd; }
    .page-footer__menulink[href]:visited {
